'use client'

import { useState, useEffect, useRef } from 'react'
import { useRouter } from 'next/navigation'
import { Button } from '@/components/ui/button'
import { Card } from '@/components/ui/card'
import { Progress } from '@/components/ui/progress'
import { Volume2, ArrowLeft, ArrowRight, RotateCw, Check, X, Lightbulb } from 'lucide-react'
import { speakWord } from '@/lib/utils/text-to-speech'
import { Word } from '@/lib/types/word'
import { getStarredWords, isWordStarred, toggleStarredWord } from '@/lib/utils/starred-manager'
import { getWordsByIds, validateSpelling } from '@/lib/utils/wordbook-manager'
import confetti from 'canvas-confetti'

interface StarredStudyClientProps {
  mode: string
}

export default function StarredStudyClient({ mode }: StarredStudyClientProps) {
  const router = useRouter()
  const [words, setWords] = useState<Word[]>([])
  const [isLoading, setIsLoading] = useState(true)

  const [currentIndex, setCurrentIndex] = useState(0)
  const [isFlipped, setIsFlipped] = useState(false)
  const [userInput, setUserInput] = useState('')
  const [showAnswer, setShowAnswer] = useState(false)
  const [isCorrect, setIsCorrect] = useState<boolean | null>(null)

  // Stats
  const [correctCount, setCorrectCount] = useState(0)
  const [wrongCount, setWrongCount] = useState(0)
  const [showResults, setShowResults] = useState(false)
  const [wordResults, setWordResults] = useState<Array<{ word: Word; isCorrect: boolean }>>([])

  // Choice mode state
  const [options, setOptions] = useState<string[]>([])
  const [selectedOption, setSelectedOption] = useState<number | null>(null)

  // Matching mode state
  const [matchingPairs, setMatchingPairs] = useState<{ left: number | null; right: number | null }>({ left: null, right: null })
  const [matchedPairs, setMatchedPairs] = useState<Set<number>>(new Set())
  const [leftColumn, setLeftColumn] = useState<Word[]>([])
  const [rightColumn, setRightColumn] = useState<Word[]>([])

  // Starred state for results page
  const [starredIds, setStarredIds] = useState<Set<string>>(new Set())

  // Load words
  useEffect(() => {
    const loadWords = () => {
      const ids = getStarredWords()
      const loadedWords = getWordsByIds(ids)
      setWords(loadedWords)
      setIsLoading(false)
    }
    loadWords()
  }, [])

  const currentWord = words[currentIndex]
  const total = words.length
  const accuracy = correctCount + wrongCount > 0
    ? Math.round((correctCount / (correctCount + wrongCount)) * 100)
    : 0

  // Initialize Choice Mode Options
  useEffect(() => {
    if (mode === 'choice' && currentWord && words.length > 0) {
      generateOptions()
    }
  }, [currentIndex, mode, words])

  // Initialize Matching Mode Columns
  useEffect(() => {
    if (mode === 'matching' && words.length > 0) {
      initializeMatchingRound()
    }
  }, [mode, words])

  // Confetti effect when results are shown
  useEffect(() => {
    if (showResults) {
      // Initialize starred state for results page
      const currentStarred = new Set<string>()
      wordResults.forEach(result => {
        if (isWordStarred(result.word.id)) {
          currentStarred.add(String(result.word.id))
        }
      })
      setStarredIds(currentStarred)

      const duration = 3000;
      const animationEnd = Date.now() + duration;
      const defaults = { startVelocity: 30, spread: 360, ticks: 60, zIndex: 0 };

      const randomInRange = (min: number, max: number) => {
        return Math.random() * (max - min) + min;
      }

      const interval: any = setInterval(function () {
        const timeLeft = animationEnd - Date.now();

        if (timeLeft <= 0) {
          return clearInterval(interval);
        }

        const particleCount = 50 * (timeLeft / duration);
        confetti({
          ...defaults,
          particleCount,
          origin: { x: randomInRange(0.1, 0.3), y: Math.random() - 0.2 },
          colors: ['#E85D75', '#FF9A9E', '#FECFEF', '#FFD6E0']
        });
        confetti({
          ...defaults,
          particleCount,
          origin: { x: randomInRange(0.7, 0.9), y: Math.random() - 0.2 },
          colors: ['#E85D75', '#FF9A9E', '#FECFEF', '#FFD6E0']
        });
      }, 250);

      return () => clearInterval(interval);
    }
  }, [showResults])

  // Keyboard shortcuts
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if (showResults) return

      if (mode === 'flashcard') {
        if (e.code === 'Space') {
          e.preventDefault()
          setIsFlipped(prev => !prev)
        } else if (e.code === 'ArrowRight') {
          handleNext()
        } else if (e.code === 'ArrowLeft') {
          handlePrev()
        }
      } else if (mode === 'dictation') {
        if (e.key === 'Enter') {
          if (showAnswer) {
            handleNext()
          } else {
            checkDictation()
          }
        }
      }
    }

    window.addEventListener('keydown', handleKeyDown)
    return () => window.removeEventListener('keydown', handleKeyDown)
  }, [mode, showResults, isFlipped, showAnswer, userInput, currentIndex])

  // Play audio when current word changes
  useEffect(() => {
    if (currentWord && !showResults) {
      // Small delay to ensure smooth transition
      const timer = setTimeout(() => {
        speakWord(currentWord.word)
      }, 300)
      return () => clearTimeout(timer)
    }
  }, [currentIndex, currentWord, showResults])



  const generateOptions = () => {
    if (!currentWord) return

    // Get 3 random wrong options
    const otherWords = words.filter(w => w.id !== currentWord.id)
    const wrongOptions = otherWords
      .sort(() => 0.5 - Math.random())
      .slice(0, 3)
      .map(w => w.translation)

    const allOptions = [...wrongOptions, currentWord.translation]
      .sort(() => 0.5 - Math.random())

    setOptions(allOptions)
    setSelectedOption(null)
    setShowAnswer(false)
    setIsCorrect(null)
  }

  const initializeMatchingRound = () => {
    // Take next 5 words or remaining words
    const roundSize = 5
    const roundWords = words.slice(currentIndex, currentIndex + roundSize)

    if (roundWords.length === 0) {
      setShowResults(true)
      return
    }

    setLeftColumn([...roundWords].sort(() => 0.5 - Math.random()))
    setRightColumn([...roundWords].sort(() => 0.5 - Math.random()))
    setMatchedPairs(new Set())
    setMatchingPairs({ left: null, right: null })
  }

  const handleNext = () => {
    if (currentIndex < total - 1) {
      setCurrentIndex(prev => prev + 1)
      setIsFlipped(false)
      setUserInput('')
      setShowAnswer(false)
      setIsCorrect(null)
    } else {
      setShowResults(true)
    }
  }

  const handlePrev = () => {
    if (currentIndex > 0) {
      setCurrentIndex(prev => prev - 1)
      setIsFlipped(false)
      setUserInput('')
      setShowAnswer(false)
      setIsCorrect(null)
    }
  }

  const checkDictation = () => {
    if (!userInput.trim()) return

    const validation = validateSpelling(userInput, currentWord.word)
    setIsCorrect(validation.isCorrect)
    setShowAnswer(true)

    if (validation.isCorrect) {
      setCorrectCount(prev => prev + 1)
      setWordResults(prev => [...prev, { word: currentWord, isCorrect: true }])
      // Auto advance after delay
      setTimeout(handleNext, 1000)
    } else {
      setWrongCount(prev => prev + 1)
      setWordResults(prev => [...prev, { word: currentWord, isCorrect: false }])
    }
  }

  const checkChoice = (index: number) => {
    if (showAnswer) return

    setSelectedOption(index)
    setShowAnswer(true)

    const isRight = options[index] === currentWord.translation
    setIsCorrect(isRight)

    if (isRight) {
      setCorrectCount(prev => prev + 1)
      setWordResults(prev => [...prev, { word: currentWord, isCorrect: true }])
      setTimeout(handleNext, 1000)
    } else {
      setWrongCount(prev => prev + 1)
      setWordResults(prev => [...prev, { word: currentWord, isCorrect: false }])
    }
  }

  const checkMatching = (side: 'left' | 'right', index: number) => {
    const newPairs = { ...matchingPairs, [side]: index }
    setMatchingPairs(newPairs)

    if (newPairs.left !== null && newPairs.right !== null) {
      const leftWord = leftColumn[newPairs.left]
      const rightWord = rightColumn[newPairs.right]

      if (leftWord.id === rightWord.id) {
        // Match found
        const newMatched = new Set(matchedPairs)
        newMatched.add(leftWord.id as number)
        setMatchedPairs(newMatched)
        setMatchingPairs({ left: null, right: null })
        setCorrectCount(prev => prev + 1)
        setWordResults(prev => [...prev, { word: leftWord, isCorrect: true }])

        // Check if round complete
        if (newMatched.size === leftColumn.length) {
          setTimeout(() => {
            const nextIndex = currentIndex + leftColumn.length
            if (nextIndex >= total) {
              setShowResults(true)
            } else {
              setCurrentIndex(nextIndex)
              // Initialize next round will be triggered by useEffect on currentIndex change? 
              // No, initializeMatchingRound depends on words/mode. 
              // We need to manually trigger or rely on effect.
              // Actually, let's just call initializeMatchingRound directly but we need updated currentIndex.
              // Better to let useEffect handle it? But useEffect depends on mode/words.
              // Let's modify useEffect to depend on currentIndex for matching too?
              // Or just force update.
            }
          }, 1000)
        }
      } else {
        // No match
        setWrongCount(prev => prev + 1)
        setWordResults(prev => [...prev, { word: leftWord, isCorrect: false }]) // Just log one of them
        setTimeout(() => {
          setMatchingPairs({ left: null, right: null })
        }, 500)
      }
    }
  }

  // Fix for matching mode round transition
  useEffect(() => {
    if (mode === 'matching' && words.length > 0) {
      // Only re-init if we moved past the current batch
      // This is tricky. Simplified: just re-init when currentIndex changes significantly?
      // Actually, initializeMatchingRound uses currentIndex.
      initializeMatchingRound()
    }
  }, [currentIndex, mode, words]) // Added currentIndex

  const renderHeader = () => (
    <header className="bg-white border-b border-gray-100 py-3 md:py-4 sticky top-0 z-50">
      <div className="container mx-auto px-4 md:px-6 flex items-center justify-between">
        <div className="flex items-center gap-4">
          <Button
            variant="ghost"
            size="sm"
            onClick={() => router.push('/starred')}
            className="text-gray-500 hover:text-[#E85D75] hover:bg-[#FFF0F3]"
          >
            <ArrowLeft className="w-4 h-4 mr-1" />
            ÈÄÄÂá∫
          </Button>
          <div className="h-4 w-px bg-gray-200"></div>
          <h1 className="text-base md:text-lg font-bold text-gray-900">
            ÊòüÊ†áÂçïËØçÊú¨
          </h1>
        </div>

        <div className="flex items-center gap-2">
          <div className="text-sm font-medium text-gray-500">
            {currentIndex + 1} / {total}
          </div>
        </div>
      </div>
    </header>
  )

  const renderProgressBar = () => (
    <div className="w-full h-1 bg-gray-100">
      <div
        className="h-full bg-[#E85D75] transition-all duration-300"
        style={{ width: `${((currentIndex + 1) / total) * 100}%` }}
      />
    </div>
  )

  const renderFlashcard = () => (
    <div className="flex-1 flex flex-col items-center justify-center p-4 md:p-8 relative">
      <div
        className="w-full max-w-xl aspect-[4/3] md:aspect-[3/2] perspective-1000 cursor-pointer group"
        onClick={() => setIsFlipped(!isFlipped)}
      >
        <div className={`relative w-full h-full transition-all duration-500 transform-style-3d ${isFlipped ? 'rotate-y-180' : ''}`}>
          {/* Front */}
          <div className="absolute w-full h-full backface-hidden bg-white rounded-3xl shadow-xl border-2 border-gray-100 p-8 md:p-12 flex flex-col items-center justify-center hover:border-pink-100 hover:shadow-2xl transition-all">
            <div className="text-4xl md:text-6xl font-bold text-gray-900 mb-6 text-center">
              {currentWord?.word}
            </div>
            <div className="flex items-center gap-2 text-muted-foreground bg-gray-50 px-4 py-2 rounded-full">
              <Volume2 className="w-4 h-4" />
              <span className="font-mono text-lg">{currentWord?.phonetic}</span>
            </div>
            <div className="absolute bottom-8 text-sm text-gray-400 animate-bounce">
              ÁÇπÂáªÁøªËΩ¨
            </div>
          </div>

          {/* Back */}
          <div className="absolute w-full h-full backface-hidden rotate-y-180 bg-[#FFF5F8] rounded-3xl shadow-xl border-2 border-pink-100 p-8 md:p-12 flex flex-col items-center justify-center">
            <div className="text-2xl md:text-3xl font-medium text-gray-800 text-center mb-4">
              {currentWord?.translation}
            </div>
            {currentWord?.definition && (
              <div className="text-base text-gray-600 text-center max-w-md">
                {currentWord.definition}
              </div>
            )}
          </div>
        </div>
      </div>

      {/* Controls */}
      <div className="flex items-center gap-6 mt-8 md:mt-12">
        <Button
          variant="outline"
          size="lg"
          onClick={(e) => { e.stopPropagation(); handlePrev() }}
          disabled={currentIndex === 0}
          className="h-14 w-14 rounded-full border-2"
        >
          <ArrowLeft className="w-6 h-6" />
        </Button>

        <Button
          size="lg"
          onClick={(e) => { e.stopPropagation(); speakWord(currentWord.word) }}
          className="h-16 w-16 rounded-full bg-[#E85D75] hover:bg-[#D84B67] shadow-lg hover:shadow-xl hover:scale-105 transition-all"
        >
          <Volume2 className="w-8 h-8" />
        </Button>

        <Button
          variant="outline"
          size="lg"
          onClick={(e) => { e.stopPropagation(); handleNext() }}
          disabled={currentIndex === total - 1}
          className="h-14 w-14 rounded-full border-2"
        >
          <ArrowRight className="w-6 h-6" />
        </Button>
      </div>
    </div>
  )

  const renderDictation = () => (
    <div className="flex-1 flex flex-col items-center justify-center p-4 md:p-8 max-w-2xl mx-auto w-full">
      <div className="w-full bg-white rounded-3xl shadow-lg border border-gray-100 p-8 md:p-12 text-center">
        <div className="mb-8">
          <div className="text-2xl md:text-3xl font-medium text-gray-800 mb-2">
            {currentWord?.translation}
          </div>
          <div className="flex justify-center mt-4">
            <Button
              variant="ghost"
              size="sm"
              onClick={() => speakWord(currentWord.word)}
              className="text-[#E85D75] hover:bg-[#FFF0F3]"
            >
              <Volume2 className="w-5 h-5 mr-2" />
              ÂÜçÂê¨‰∏ÄÈÅç
            </Button>
          </div>
        </div>

        <div className="relative mb-8">
          <input
            type="text"
            value={userInput}
            onChange={(e) => setUserInput(e.target.value)}
            onKeyDown={(e) => e.stopPropagation()} // Prevent global shortcuts
            placeholder="ËæìÂÖ•ÂçïËØç..."
            disabled={showAnswer}
            className={`w-full text-center text-3xl md:text-4xl font-bold bg-transparent border-b-2 py-4 focus:outline-none transition-colors ${showAnswer
              ? isCorrect
                ? 'border-green-500 text-green-600'
                : 'border-red-500 text-red-600'
              : 'border-gray-200 focus:border-[#E85D75]'
              }`}
            autoFocus
          />
          {showAnswer && !isCorrect && (
            <div className="mt-4 text-xl font-bold text-green-600 animate-in fade-in slide-in-from-bottom-2">
              {currentWord?.word}
            </div>
          )}
        </div>

        <div className="flex gap-3 justify-center">
          <Button
            variant="ghost"
            onClick={() => {
              setShowAnswer(true)
              setWrongCount(prev => prev + 1)
              setWordResults(prev => [...prev, { word: currentWord, isCorrect: false }])
            }}
            disabled={showAnswer}
            className="text-gray-400 hover:text-[#E85D75]"
          >
            <Lightbulb className="w-4 h-4 mr-2" />
            ÊèêÁ§∫
          </Button>
          {!showAnswer ? (
            <Button
              onClick={checkDictation}
              className="bg-[#E85D75] hover:bg-[#D84B67] text-white px-8 rounded-xl"
            >
              Á°ÆËÆ§
            </Button>
          ) : (
            <Button
              onClick={handleNext}
              className={`px-8 rounded-xl ${isCorrect ? 'bg-green-500 hover:bg-green-600' : 'bg-[#E85D75] hover:bg-[#D84B67]'}`}
            >
              ‰∏ã‰∏Ä‰∏™
              <ArrowRight className="w-4 h-4 ml-2" />
            </Button>
          )}
        </div>
      </div>
    </div>
  )

  const renderChoice = () => (
    <div className="flex-1 flex flex-col items-center justify-center p-4 md:p-8 max-w-2xl mx-auto w-full">
      <div className="w-full bg-white rounded-3xl shadow-lg border border-gray-100 p-8 md:p-10">
        <div className="text-center mb-8">
          <h2 className="text-4xl md:text-5xl font-bold text-gray-900 mb-4">
            {currentWord?.word}
          </h2>
          <div className="flex justify-center">
            <Button
              variant="ghost"
              size="sm"
              onClick={() => speakWord(currentWord.word)}
              className="bg-gray-50 hover:bg-gray-100 rounded-full px-4"
            >
              <Volume2 className="w-4 h-4 mr-2" />
              {currentWord?.phonetic}
            </Button>
          </div>
        </div>

        <div className="grid gap-3">
          {options.map((option, idx) => {
            let stateClass = 'border-gray-200 hover:border-[#E85D75] hover:bg-[#FFF5F8]'
            if (showAnswer) {
              if (option === currentWord.translation) {
                stateClass = 'border-green-500 bg-green-50 text-green-700'
              } else if (selectedOption === idx) {
                stateClass = 'border-red-500 bg-red-50 text-red-700'
              } else {
                stateClass = 'border-gray-100 opacity-50'
              }
            }

            return (
              <button
                key={idx}
                onClick={() => checkChoice(idx)}
                disabled={showAnswer}
                className={`w-full p-4 text-left text-lg font-medium border-2 rounded-xl transition-all duration-200 ${stateClass}`}
              >
                <div className="flex items-center justify-between">
                  <span>{option}</span>
                  {showAnswer && option === currentWord.translation && (
                    <Check className="w-5 h-5 text-green-600" />
                  )}
                  {showAnswer && selectedOption === idx && option !== currentWord.translation && (
                    <X className="w-5 h-5 text-red-600" />
                  )}
                </div>
              </button>
            )
          })}
        </div>
      </div>
    </div>
  )

  const renderMatching = () => (
    <div className="flex-1 flex flex-col items-center justify-center p-4 md:p-8 max-w-5xl mx-auto w-full">
      <div className="grid grid-cols-2 gap-8 w-full">
        {/* Left Column (Translation) */}
        <div className="space-y-4">
          {leftColumn.map((word, idx) => {
            const isMatched = matchedPairs.has(word.id as number)
            const isSelected = matchingPairs.left === idx

            return (
              <button
                key={`left-${idx}`}
                onClick={() => checkMatching('left', idx)}
                disabled={isMatched}
                className={`w-full p-4 md:p-6 text-left text-base md:text-lg font-medium border-2 rounded-xl transition-all duration-200 ${isMatched
                  ? 'opacity-0 pointer-events-none'
                  : isSelected
                    ? 'border-[#E85D75] bg-[#FFF0F3] text-[#E85D75] shadow-md scale-105'
                    : 'border-gray-200 bg-white hover:border-pink-200 hover:shadow-sm'
                  }`}
              >
                {word.translation}
              </button>
            )
          })}
        </div>

        {/* Right Column (Word) */}
        <div className="space-y-4">
          {rightColumn.map((word, idx) => {
            const isMatched = matchedPairs.has(word.id as number)
            const isSelected = matchingPairs.right === idx

            return (
              <button
                key={`right-${idx}`}
                onClick={() => checkMatching('right', idx)}
                disabled={isMatched}
                className={`w-full p-4 md:p-6 text-center text-xl md:text-2xl font-bold border-2 rounded-xl transition-all duration-200 ${isMatched
                  ? 'opacity-0 pointer-events-none'
                  : isSelected
                    ? 'border-[#E85D75] bg-[#FFF0F3] text-[#E85D75] shadow-md scale-105'
                    : 'border-gray-200 bg-white hover:border-pink-200 hover:shadow-sm'
                  }`}
              >
                {word.word}
              </button>
            )
          })}
        </div>
      </div>
    </div>
  )

  const renderResults = () => (
    <div className="min-h-screen bg-[#FAFAFA] flex flex-col font-sans">
      {renderHeader()}

      <div className="flex-1 flex items-center justify-center p-4 md:p-8">
        <Card className="p-6 md:p-8 max-w-3xl w-full bg-white rounded-3xl shadow-lg border-0">
          {/* Title */}
          <div className="text-center mb-6">
            <h2 className="text-2xl md:text-3xl font-bold text-gray-900 flex items-center justify-center gap-2">
              üéâ ÊµãËØïÂÆåÊàêÔºÅ
            </h2>
          </div>

          {/* Stats Cards */}
          <div className="grid grid-cols-3 gap-3 md:gap-4 mb-6">
            {/* Correct */}
            <div className="text-center p-4 md:p-6 bg-[#E8F5E9] rounded-2xl">
              <div className="text-3xl md:text-4xl font-bold text-[#4CAF50] mb-1">{correctCount}</div>
              <div className="text-xs md:text-sm font-medium text-[#4CAF50]/70">Ê≠£Á°Æ</div>
            </div>

            {/* Wrong */}
            <div className="text-center p-4 md:p-6 bg-[#FFEBEE] rounded-2xl">
              <div className="text-3xl md:text-4xl font-bold text-[#F44336] mb-1">{wrongCount}</div>
              <div className="text-xs md:text-sm font-medium text-[#F44336]/70">ÈîôËØØ</div>
            </div>

            {/* Accuracy */}
            <div className="text-center p-4 md:p-6 bg-[#E3F2FD] rounded-2xl">
              <div className="text-3xl md:text-4xl font-bold text-[#2196F3] mb-1">{accuracy}%</div>
              <div className="text-xs md:text-sm font-medium text-[#2196F3]/70">Ê≠£Á°ÆÁéá</div>
            </div>
          </div>

          {/* Detailed Results */}
          <div className="mb-6">
            <h3 className="text-base md:text-lg font-bold text-gray-900 mb-3">ËØ¶ÁªÜÁªìÊûú</h3>
            <div className="max-h-96 overflow-y-auto p-1">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
                {[...wordResults]
                  .sort((a, b) => (a.isCorrect === b.isCorrect ? 0 : a.isCorrect ? 1 : -1))
                  .map((result, idx) => (
                    <div
                      key={idx}
                      className={`flex items-center gap-2 md:gap-3 p-3 rounded-xl border transition-all ${result.isCorrect
                        ? 'bg-green-50 border-green-200'
                        : 'bg-red-50 border-red-200'
                        }`}
                    >
                      {/* Status Icon */}
                      <div className={`flex-shrink-0 w-6 h-6 md:w-7 md:h-7 rounded-full flex items-center justify-center ${result.isCorrect ? 'bg-green-500' : 'bg-red-500'
                        }`}>
                        {result.isCorrect ? (
                          <svg width="14" height="14" viewBox="0 0 24 24" fill="none" stroke="white" strokeWidth="3" strokeLinecap="round" strokeLinejoin="round">
                            <polyline points="20 6 9 17 4 12"></polyline>
                          </svg>
                        ) : (
                          <svg width="14" height="14" viewBox="0 0 24 24" fill="none" stroke="white" strokeWidth="3" strokeLinecap="round" strokeLinejoin="round">
                            <line x1="18" y1="6" x2="6" y2="18"></line>
                            <line x1="6" y1="6" x2="18" y2="18"></line>
                          </svg>
                        )}
                      </div>

                      {/* Word Info */}
                      <div className="flex-1 min-w-0">
                        <div className="flex items-center gap-2">
                          <span className="font-bold text-base md:text-lg text-gray-900">{result.word.word}</span>
                          <button
                            onClick={() => speakWord(result.word.word)}
                            className="p-1 hover:bg-white/60 rounded-full transition-colors flex-shrink-0"
                          >
                            <Volume2 className="w-3.5 h-3.5 md:w-4 md:h-4 text-gray-600" />
                          </button>
                        </div>
                        <div className="text-xs md:text-sm text-gray-600 truncate">{result.word.definition}</div>
                      </div>

                      {/* Star Button */}
                      <button
                        onClick={() => {
                          const idStr = String(result.word.id)
                          const newStarred = toggleStarredWord(result.word.id)
                          setStarredIds(prev => {
                            const next = new Set(prev)
                            if (newStarred) {
                              next.add(idStr)
                            } else {
                              next.delete(idStr)
                            }
                            return next
                          })
                        }}
                        className="flex-shrink-0 p-1.5 hover:bg-white/60 rounded-full transition-colors"
                      >
                        {starredIds.has(String(result.word.id)) ? (
                          <svg width="18" height="18" viewBox="0 0 24 24" fill="#FBC02D" stroke="none">
                            <polygon points="12 2 15.09 8.26 22 9.27 17 14.14 18.18 21.02 12 17.77 5.82 21.02 7 14.14 2 9.27 8.91 8.26 12 2"></polygon>
                          </svg>
                        ) : (
                          <svg width="18" height="18" viewBox="0 0 24 24" fill="none" stroke="#D1D5DB" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                            <polygon points="12 2 15.09 8.26 22 9.27 17 14.14 18.18 21.02 12 17.77 5.82 21.02 7 14.14 2 9.27 8.91 8.26 12 2"></polygon>
                          </svg>
                        )}
                      </button>
                    </div>
                  ))}
              </div>
            </div>
          </div>

          {/* Action Buttons */}
          <div className="flex gap-4">
            <Button
              onClick={() => {
                setShowResults(false)
                setCurrentIndex(0)
                setCorrectCount(0)
                setWrongCount(0)
                setWordResults([])
                setStarredIds(new Set())
              }}
              className="flex-1 bg-gray-100 hover:bg-gray-200 text-gray-800 h-12 rounded-xl font-medium"
            >
              ÂÜçÊµã‰∏ÄÊ¨°
            </Button>
            <Button
              onClick={() => router.push('/starred')}
              className="flex-1 bg-gradient-to-r from-[#E85D75] to-[#FF9A9E] hover:opacity-90 text-white h-12 rounded-xl font-medium"
            >
              ËøîÂõûÂàóË°®
            </Button>
          </div>
        </Card>
      </div>
    </div>
  )

  if (isLoading) {
    return <div className="min-h-screen flex items-center justify-center">Âä†ËΩΩ‰∏≠...</div>
  }

  if (words.length === 0) {
    return (
      <div className="min-h-screen flex flex-col items-center justify-center gap-4">
        <p>Ê≤°ÊúâÊòüÊ†áÂçïËØç</p>
        <Button onClick={() => router.push('/starred')}>ËøîÂõûÂàóË°®</Button>
      </div>
    )
  }

  if (showResults) {
    return renderResults()
  }

  return (
    <div className="min-h-screen bg-[#FAFAFA] flex flex-col font-sans">
      {renderHeader()}
      {renderProgressBar()}

      <div className="flex-1 flex flex-col">
        {mode === 'flashcard' && renderFlashcard()}
        {mode === 'dictation' && renderDictation()}
        {mode === 'choice' && renderChoice()}
        {mode === 'matching' && renderMatching()}
      </div>
    </div>
  )
}
