/**
 * ÊñáÊú¨ËΩ¨ËØ≠Èü≥Â∑•ÂÖ∑
 * ‰ΩøÁî®ÊµèËßàÂô®ÂÜÖÁΩÆÁöÑ Web Speech API
 */

export class TextToSpeech {
    private static instance: TextToSpeech
    private synth: SpeechSynthesis | null = null
    private voices: SpeechSynthesisVoice[] = []
    private isInitialized = false

    private constructor() {
        if (typeof window !== 'undefined') {
            this.synth = window.speechSynthesis
            this.loadVoices()
        }
    }

    static getInstance(): TextToSpeech {
        if (!TextToSpeech.instance) {
            TextToSpeech.instance = new TextToSpeech()
        }
        return TextToSpeech.instance
    }

    /**
     * Âä†ËΩΩÂèØÁî®ÁöÑËØ≠Èü≥ÂàóË°®
     */
    private loadVoices() {
        if (!this.synth) return

        const loadVoicesCallback = () => {
            this.voices = this.synth!.getVoices()
            this.isInitialized = true
        }

        // Chrome ÈúÄË¶ÅÁ≠âÂæÖ voiceschanged ‰∫ã‰ª∂
        if (this.synth.onvoiceschanged !== undefined) {
            this.synth.onvoiceschanged = loadVoicesCallback
        }

        // Á´ãÂç≥Â∞ùËØïÂä†ËΩΩ‰∏ÄÊ¨°
        loadVoicesCallback()
    }

    /**
   * Ëé∑ÂèñËã±ËØ≠ËØ≠Èü≥
   * ‰ºòÂÖàÈÄâÊã©È´òË¥®ÈáèÁöÑËØ≠Èü≥ÂºïÊìé
   */
    private getEnglishVoice(): SpeechSynthesisVoice | null {
        if (!this.isInitialized || this.voices.length === 0) {
            return null
        }

        // È´òË¥®ÈáèËØ≠Èü≥‰ºòÂÖàÁ∫ßÂàóË°®ÔºàÊåâË¥®ÈáèÊéíÂ∫èÔºâ
        const preferredVoices = [
            // Google ËØ≠Èü≥ÔºàË¥®ÈáèÊúÄÂ•ΩÔºâ
            'Google US English',
            'Google UK English Female',
            'Google UK English Male',

            // Microsoft ËØ≠Èü≥ÔºàË¥®ÈáèÂ•ΩÔºâ
            'Microsoft Zira - English (United States)',
            'Microsoft David - English (United States)',
            'Microsoft Mark - English (United States)',
            'Microsoft Aria - English (United States)',

            // Apple ËØ≠Èü≥ÔºàMac/iOSÔºâ
            'Samantha',
            'Alex',
            'Karen',
            'Daniel',

            // ÂÖ∂‰ªñÈ´òË¥®ÈáèËØ≠Èü≥
            'en-US-Neural2-A',
            'en-US-Neural2-C',
            'en-US-Wavenet-A',
            'en-US-Wavenet-C',
        ]

        // 1. È¶ñÂÖàÂ∞ùËØïÂåπÈÖçÈ´òË¥®ÈáèËØ≠Èü≥
        for (const preferredName of preferredVoices) {
            const voice = this.voices.find(v =>
                v.name.includes(preferredName) ||
                v.name === preferredName
            )
            if (voice) {
                console.log('üé§ Selected high-quality voice:', voice.name)
                return voice
            }
        }

        // 2. Â¶ÇÊûúÊ≤°ÊúâÈ´òË¥®ÈáèËØ≠Èü≥ÔºåÂ∞ùËØï‰ªª‰ΩïËã±ËØ≠ËØ≠Èü≥
        const anyEnglishVoice = this.voices.find(v => v.lang.startsWith('en'))
        if (anyEnglishVoice) {
            return anyEnglishVoice
        }

        return null
    }

    /**
     * ÊúóËØªÊñáÊú¨
     * @param text Ë¶ÅÊúóËØªÁöÑÊñáÊú¨
     * @param options ÊúóËØªÈÄâÈ°π
     */
    speak(
        text: string,
        options: {
            rate?: number // ËØ≠ÈÄü (0.1 - 10, ÈªòËÆ§ 1)
            pitch?: number // Èü≥Ë∞É (0 - 2, ÈªòËÆ§ 1)
            volume?: number // Èü≥Èáè (0 - 1, ÈªòËÆ§ 1)
            lang?: string // ËØ≠Ë®Ä (ÈªòËÆ§ en-US)
            onEnd?: () => void // ÊúóËØªÁªìÊùüÂõûË∞É
            onError?: (error: SpeechSynthesisErrorEvent) => void // ÈîôËØØÂõûË∞É
        } = {}
    ): void {
        if (!this.synth) {
            console.warn('Speech synthesis not supported in this browser')
            return
        }

        // ÂÅúÊ≠¢ÂΩìÂâçÊ≠£Âú®Êí≠ÊîæÁöÑËØ≠Èü≥
        this.stop()

        const utterance = new SpeechSynthesisUtterance(text)

        // ËÆæÁΩÆËØ≠Èü≥
        const voice = this.getEnglishVoice()
        if (voice) {
            utterance.voice = voice
        }

        // ‰ºòÂåñÂêéÁöÑÂèÇÊï∞ËÆæÁΩÆ
        utterance.rate = options.rate ?? 1.0  // Ê≠£Â∏∏ËØ≠ÈÄüÔºåÊõ¥Ëá™ÁÑ∂
        utterance.pitch = options.pitch ?? 1.05  // Á®çÂæÆÊèêÈ´òÈü≥Ë∞ÉÔºåÊõ¥Ê∏ÖÊô∞
        utterance.volume = options.volume ?? 1.0  // ÊúÄÂ§ßÈü≥Èáè
        utterance.lang = options.lang ?? 'en-US'

        // ËÆæÁΩÆÂõûË∞É
        if (options.onEnd) {
            utterance.onend = options.onEnd
        }

        if (options.onError) {
            utterance.onerror = options.onError
        }

        // ÂºÄÂßãÊúóËØª
        this.synth.speak(utterance)
    }

    /**
     * ÊúóËØªÂçïËØçÔºà‰∏ìÈó®‰∏∫ÂçïËØçÂ≠¶‰π†‰ºòÂåñÔºâ
     * @param word ÂçïËØç
     * @param options ÈÄâÈ°π
     */
    speakWord(
        word: string,
        options: {
            slow?: boolean // ÊòØÂê¶ÊÖ¢ÈÄüÊúóËØª
            repeat?: number // ÈáçÂ§çÊ¨°Êï∞ÔºàÈªòËÆ§1Ê¨°Ôºâ
            onComplete?: () => void // ÂÖ®ÈÉ®ÂÆåÊàêÂõûË∞É
        } = {}
    ): void {
        const { slow = false, repeat = 1, onComplete } = options
        let count = 0

        const speakOnce = () => {
            count++
            this.speak(word, {
                rate: slow ? 0.75 : 1.0,  // ÊÖ¢ÈÄüÊó∂0.75ÔºåÊ≠£Â∏∏Êó∂1.0
                pitch: 1.05,  // Á®çÂæÆÊèêÈ´òÈü≥Ë∞É
                onEnd: () => {
                    if (count < repeat) {
                        // ÈáçÂ§çÊúóËØªÔºåÈó¥Èöî600ms
                        setTimeout(speakOnce, 600)
                    } else if (onComplete) {
                        onComplete()
                    }
                },
            })
        }

        speakOnce()
    }

    /**
     * ÊúóËØª‰æãÂè•
     * @param sentence ‰æãÂè•
     * @param options ÈÄâÈ°π
     */
    speakSentence(
        sentence: string,
        options: {
            rate?: number
            onEnd?: () => void
        } = {}
    ): void {
        this.speak(sentence, {
            rate: options.rate ?? 0.95,  // ‰æãÂè•Á®çÊÖ¢ÔºåÊõ¥ÂÆπÊòìÁêÜËß£
            pitch: 1.05,  // ‰øùÊåÅÊ∏ÖÊô∞ÁöÑÈü≥Ë∞É
            onEnd: options.onEnd,
        })
    }

    /**
     * ÂÅúÊ≠¢ÊúóËØª
     */
    stop(): void {
        if (this.synth) {
            this.synth.cancel()
        }
    }

    /**
     * ÊöÇÂÅúÊúóËØª
     */
    pause(): void {
        if (this.synth) {
            this.synth.pause()
        }
    }

    /**
     * ÊÅ¢Â§çÊúóËØª
     */
    resume(): void {
        if (this.synth) {
            this.synth.resume()
        }
    }

    /**
     * Ê£ÄÊü•ÊòØÂê¶Ê≠£Âú®ÊúóËØª
     */
    isSpeaking(): boolean {
        return this.synth?.speaking ?? false
    }

    /**
     * Ëé∑ÂèñÂèØÁî®ÁöÑËØ≠Èü≥ÂàóË°®
     */
    getAvailableVoices(): SpeechSynthesisVoice[] {
        return this.voices
    }

    /**
     * ÂàóÂá∫ÊâÄÊúâÂèØÁî®ÁöÑËã±ËØ≠ËØ≠Èü≥ÔºàË∞ÉËØïÁî®Ôºâ
     */
    listEnglishVoices(): void {
        const englishVoices = this.voices.filter(v => v.lang.startsWith('en'))
        console.log('üì¢ Available English Voices:')
        englishVoices.forEach((voice, index) => {
            console.log(`${index + 1}. ${voice.name} (${voice.lang}) ${voice.default ? '‚≠ê DEFAULT' : ''}`)
        })
        console.log(`\nTotal: ${englishVoices.length} English voices available`)

        const currentVoice = this.getEnglishVoice()
        if (currentVoice) {
            console.log(`\nüé§ Currently selected: ${currentVoice.name}`)
        }
    }
}

// ÂØºÂá∫Âçï‰æãÂÆû‰æã
export const tts = typeof window !== 'undefined' ? TextToSpeech.getInstance() : null

// ‰æøÊç∑ÂáΩÊï∞
export const speakWord = (word: string, options?: Parameters<TextToSpeech['speakWord']>[1]) => {
    tts?.speakWord(word, options)
}

export const speakSentence = (sentence: string, options?: Parameters<TextToSpeech['speakSentence']>[1]) => {
    tts?.speakSentence(sentence, options)
}

export const stopSpeaking = () => {
    tts?.stop()
}
